import addToMailchimp from "gatsby-plugin-mailchimp"
import React, { useState } from "react"
import styled from "styled-components"

const NewsletterWrapper = styled.form`
  display: flex;
`
const DescriptionWrapper = styled.div`
  margin: auto;
  p {
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
  }
  h2  {
    text-align: center;
  }

  a {
    color: green;
  }
`

const InputWrapper = styled.div`
margin-left: auto;
`

const Input = styled.input`
  font-size: 1rem;
  padding: 20px; 
  // width: 400px; 
  margin-bottom: 20px; 
  border-bottom-width: 2px; 
  border-bottom-style: solid; 
  border-bottom-color: #b5dd00; 
  border-top-style: none; 
  border-right-style: none; 
  border-left-style: none; 
`

const ButtonWrapper = styled.div`
margin-right: auto;
display: flex;
`

const Button = styled.button`
  margin: auto;
  padding: 10px 20px;
  background-color: #b5dd00;
  box-shadow: rgba(0,0,0,0.25) 0 2px 5px;
  font-size: 0.8rem;
  border: none;
  border-radius: 25px;
  font-weight: bold;
`

export default function SubscriptionForm() {
  const [email, setEmail] = useState("")
  const [submitted, setSubmitted] = useState(false)   


  function errorHandling(data) {
    // your error handling
  }
  
  const handleSubmit = () => {
    addToMailchimp(email).then((data) => {

      if (data.result === "error") {
        errorHandling(data)
      } else {
        // trackCustomEvent({
        //   category: "Newsletter",
        //   action: "Click",
        //   label: `Newsletter Click`,
        // })
        setSubmitted(true)
      }
    })
  }
  return (
    <>
      {submitted ? (
        <NewsletterWrapper>
          <DescriptionWrapper>
            <h2>
              Thank you!
            </h2>
            <p>
              You can download it from <br/>
                 <a target="_blank" href="https://weareready-website.oss-us-west-1.aliyuncs.com/weareready.cn-10_free_ways.pdf?versionId=CAEQNBiBgID6nY2EyhciIGFkOGU3OGU3OGQyYjQ5MmY5YWU4NjVmYTJlZWM4NWYx">HERE</a>
            </p>
          </DescriptionWrapper>
        </NewsletterWrapper>
      ) : (
        <NewsletterWrapper>
          <InputWrapper>
            <Input
              type="email"
              name="email"
              id="mail"
              label="email-input"
              placeholder="Your e-mail address"
              onChange={(e) => setEmail(e.target.value)}
            />
          </InputWrapper>
          <ButtonWrapper>
            <Button
              type="button"
              aria-label="Subscribe"
              onClick={() => handleSubmit()}
            >
              Subscribe
            </Button>
          </ButtonWrapper>
        </NewsletterWrapper>
      )}
    </>
  );
}